@media (max-width:700px){

  .end-game-popup{
    .title{
      svg,img{
        display:none;
      }
    }
    .press_F5-container{
      margin-top:15px;
    }
  }
  .lights_out_container{
      .pulsing_lights_out_button{
        animation-name:pulsing_lights_out_button_mobile;
        animation-duration:0.5s;
        animation-iteration-count: infinite;
        animation-direction:alternate;
      }
  }
}
